@import url("//hello.myfonts.net/count/3cd072");

@font-face {
  font-display: swap;
  font-family: 'TTMarxiana-Antiqua';
  src: url('/assets/fonts/ttmarxiana-antiqua/font.woff2') format('woff2'), url('/assets/fonts/ttmarxiana-antiqua/font.woff') format('woff'); }

@font-face {
  font-display: swap;
  font-family: 'TTMarxiana-Antiqua';
  font-style: italic;
  src: url('/assets/fonts/ttmarxiana-antiqua-italic/font.woff2') format('woff2'), url('/assets/fonts/ttmarxiana-antiqua-italic/font.woff') format('woff'); }

@font-face {
  font-display: swap;
  font-family: 'TTMarxiana-Grotesque';
  font-variant-ligatures: normal;
  src: url('/assets/fonts/ttmarxiana-grotesque/font.woff2') format('woff2'), url('/assets/fonts/ttmarxiana-grotesque/font.woff') format('woff'); }

* {
  font-family: 'TTMarxiana-Antiqua';
  font-variant-ligatures: discretionary-ligatures; }

h1 {
  font-style: italic;
  font-weight: normal; }

blockquote {
  font-size: 22px;
  font-style: italic;
  margin-left: 30px; }

code {
  display: block;
  font-family: 'TTMarxiana-Grotesque';
  font-size: 22px;
  margin-left: 30px; }
